import React from 'react';

import Layout from '../components/Layout';

import { Link } from 'gatsby';
import Header from '../components/Header';
import Footer from '../components/Footer';

import pic1 from '../assets/images/pic01.jpg';
import pic2 from '../assets/images/pic02.jpg';
import pic3 from '../assets/images/pic03.jpg';

const IndexPage = () => (
  <Layout>
    <Header />

    <div id="main">
      <header className="major container medium">
        <h2>
          My Projects
        </h2>
      </header>

      <div className="box alt container">
        <section className="feature left">
          <a href="https://github.com/BrandonKenter/House-Flipper" className="image icon fa-dollar">
            <img src={pic1} alt="" />
          </a>
          <div className="content">
            <h3>House Flipper</h3>
            <p>
              Application to test your understanding of real estate, the
              repair process and what goes into flipping properties.
            <p>
              <br></br>
              Built with Java/JavaFX and Scene Builder.
            </p>
             
               
            </p>
          </div>
        </section>
        <section className="feature right">
          <a href="https://github.com/BrandonKenter/Paint-Inventory" className="image icon fa-paint-brush">
            <img src={pic2} alt="" />
          </a>
          <div className="content">
            <h3>Paint Inventory</h3>
            <p>
              Inventory management system for companies that need to keep
              track of their paint inventory. It makes it easy for employees
              to see which paints are running low.
              <p>
                <br></br>
              Built with Python/PyQt5, Qt Designer and SQLite.
              </p>
            </p>
          </div>
        </section>
        <section className="feature left">
          <a href="https://github.com/BrandonKenter/Shortest-Path-Visualizer" className="image icon fa-arrow-circle-o-right">
            <img src={pic3} alt="" />
          </a>
          <div className="content">
            <h3>Shortest Path Visualizer</h3>
            <p>
              Finds the shortest path from a starting point to an ending point.
              Uses Dijkstra's algorithm and its derivative A* while representing
              the path found on the graph.
              <p>
              <br></br>
              Built with Python/PyQt5 and Qt Designer
              </p>
            </p>
          </div>
        </section>
      </div>

      <footer className="major container medium">
        
      </footer>
    </div>
    <Footer />
  </Layout>
);

export default IndexPage;
